  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

#app {
  width: 100%;
  height: 100vh;
  background: #f2f2f2;
  display: flex;
  align-items: center;
  justify-content: center;
}

.init-button {
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -50px;
  margin-top: -50px;
  font-size: 20px;
  font-family: Calibri, 'Trebuchet MS', sans-serif;
  background: transparent;
}

.init-button > div {
  z-index: 1;
}

.init-button::before {
  content: "";
  transition: transform 0.35s ease;
  transform-origin: center;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  z-index: 0;
}

.init-button:hover::before {
  transform: scale(1.5);
}

.init-button[disabled]::before {
  transform: scale(30);
}


@keyframes blink {
  50% {
    background-color: transparent;
  }
}

.rwc-message-typing {
  display: inline-block;
  padding: 16px;
  line-height: 0;
  border-radius: 8px 8px 8px 0;
  margin-bottom: 8px;
  transform-origin: bottom left;
}

.rwc-message-typing__dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 6px;
  font-size: 24px;
  border-radius: 50%;
  animation: 2s blink infinite;
  background-color: rgba(0, 0, 0, 0.6);
}

.rwc-message-typing__dot:nth-child(2) {
  animation-delay: 250ms;
}

.rwc-message-typing__dot:nth-child(3) {
  margin-right: 0;
  animation-delay: 500ms;
}


</style>
